<template>
	<view class="content">
		<view class="tops">
			<!--  -->
			<image @tap="uploadImg" :src="headUrl===''||headUrl==null?'../../static/home/500.jpg':base_url+'/img'+headUrl" mode=""></image> 
			<view class="uname">{{userName}}</view>
			<view class="uphone">{{phone}}</view>
		</view>
		<view class="person-item" @tap="tosetUser">
			<view>个人设置</view>
			<image class="tos" src="../../static/mine/tos.png" mode=""></image>
			<image src="../../static/mine/003.png" mode=""></image>
			<image src="../../static/mine/004.png" mode=""></image>
		</view>
		<view class="person-item" @tap="toCompany">
			<view>企业管理</view>
			<image class="tos" src="../../static/mine/tos.png" mode=""></image>
			<image src="../../static/mine/003.png" mode=""></image>
			<image src="../../static/mine/004.png" mode=""></image>
		</view>
		<button class="goOut" @tap='goOut'>退出登录</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone:'',
				userName:'',
				userId:'',
				headUrl:'',
				identityType:'',
			}
		},
		onLoad() {
			this.getUserInfo()
		},
		methods:{
			// 上传头像
			uploadImg(){
				uni.chooseImage({
					count:1,
					sizeType:['album'],
					success:res=>{
						console.log(res);
						if(res.errMsg=="chooseImage:ok"){
							uni.showLoading({
								title:'上传中'
							})
							let tempFilePaths = res.tempFilePaths;
							console.log(tempFilePaths)
							uni.uploadFile({
								url:this.base_url + `/user/revamp/header/${this.userId}`,
								filePath:tempFilePaths[0],
								name:'file',
								header:{
									Authorization:uni.getStorageSync('token'), 
								},
								success:res=>{
									uni.hideLoading()
									console.log(JSON.parse(res.data) ,'上传图片')
									if(JSON.parse(res.data).code==200){
										uni.showToast({
											title:'上传成功',
											icon:'none',
											duration:2000
										})
										this.getUserInfo()
									}else{
										uni.showToast({
											title:'图片上传失败',
											icon:'none'
										})
									}
								},fail:err=>{
									uni.showToast({
										title:'图片上传失败',
										icon:'none'
									})
								}
							})
						}
					}
				})
			},
			// 前往管理页面
			toCompany(){
				if(this.identityType===1){
					uni.navigateTo({
						url:'../index/enterprise'
					})					
				}else{
					uni.navigateTo({
						url:'../index/enterprise/cinform'
					})
				}
			},
			tosetUser(){
				uni.navigateTo({
					url:'./setUser/setUser'
				})
			},
			// 退出登录
			goOut(){
				uni.showModal({
					title:'确认操作',
					content:'点击确定退出登录',
					success: (res) => {
						if(res.confirm){
							uni.removeStorageSync('token')
							uni.removeStorageSync('userInfo')
							uni.reLaunch({
								url:'/pages/login'
							})							
						}
					}
				})
			},
			// 获取用户信息
			getUserInfo(){
				let api = '/user/info';
				let data = { userId:uni.getStorageSync('userInfo').userId }
				this.http(api,'GET',data).then(res=>{
					console.log(res);
					if(res.code==200){
						this.phone = res.data.phone;
						this.userName = res.data.userName;
						this.userId = res.data.userId;
						this.headUrl = res.data.headUrl;
						this.identityType = res.data.identityType
					}
				})
			},
		}
	}
</script>

<style scoped>
	.goOut{
		width: 562rpx;
		height: 78rpx;
		line-height: 78rpx;
		border:1rpx solid rgba(15,67,155,1);
		border-radius:39rpx;
		margin-top: 256rpx;
		font-size: 28rpx;
		background: transparent;
		color: #fff;
	}
	.goOut:active{
		background-color:#033163;  
	}
	.person-item{
		height: 106rpx;
		display: flex;
		align-items: center;
		padding: 0 15rpx;
		box-sizing: border-box;
		justify-content: space-between;
	}
	.person-item image:nth-of-type(2){
		width: 12rpx;
		height: 12rpx;
		bottom: -3rpx;
		left: -3rpx;
		position: absolute;
	}
	.person-item image:nth-of-type(3){
		width: 12rpx;
		height: 12rpx;
		bottom: -3rpx;
		right: -3rpx;
		position: absolute; 
	}
	.person-item view{
		font-size: 28rpx;
	}
	.person-item>.tos{
		width: 18rpx;
		height: 32rpx;
	}
	.tops{
		width: 100%;
		height: 362rpx;
		background: url(../../static/mine/005.png) no-repeat;
		background-size: 100% 100%;
		text-align: center;
		overflow: hidden;
	}
	.tops>image{
		width: 132rpx;
		height: 132rpx;
		margin-top: 44rpx;
		border-radius: 50%;
	}
	.uname{
		font-size: 36rpx;
	}
	.uphone{
		font-size: 28rpx;
	}
</style>